import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import '../css/Futbol.css';

class Futbol extends Component {
    render() {
        return (
            <div className='marcoFutbol'>
                <div className='tituloFutbol'>FTBOL ADAPTADO</div>
                <div className='futbolParalax'></div>                
                <div className='escudoFutbol'></div>
                <div className='infoInicial'>El  objetivo  principal del ftbol con  productos  de  apoyo es  incentivar  y  fomentar  la  prctica  de  ftbol  de  una  forma  adaptada  a  las  capacidades  motoras  de  nias  y  nios  que  abarcan  las  edades  entre  los  6  a  15  aos,  con  una  afectacin  motora  que  pueda  limitar  la  prctica  del  ftbol.En  su  mayora  va  dirigido  a  nios/as  con  patologa  neurolgica  con  un  nivel  cognitivo  que  le  permita  seguir  la  participacin  de  la  actividad.  Se  incluirn  inicialmente  los  niveles  de  afectacin  motora  I,  II,  III,  IV  del  Sistema  de  Clasificacin  de  la  Funcin  Motriz  (GMFCS)  o  afectaciones  similares  con  capacidad  de  usar  un  andador  posterior. INFORMATE</div>
            </div>
        );
    }
};

export default Futbol;


